import {
    ADD_CONTACT,
    DELETE_CONTACT,
    SET_CURRENT,
    CLEAR_CONTACTS,
    UPDATE_CONTACT,
    FILTER_CONTACTS,
    CLEAR_FILTER,
    CLEAR_CURRENT,
    CONTACT_ERROR,
    GET_CONTACTS
} from '../types';

export default (state,action) => {
    switch (action.type) {
        case ADD_CONTACT:
            
            return {
                ...state,
                contacts: [action.payLoad, ...state.contacts],
                loading : false
            };
        case DELETE_CONTACT:

            return {
                ...state,
                contacts: state.contacts.filter(contact=> contact._id !== action.payLoad ),
                loading : false
            };
        case SET_CURRENT:

            return {
                ...state,
                current : action.payLoad
            };
        case CLEAR_CURRENT:

            return {
                ...state,
                current : null
            };
        case UPDATE_CONTACT:

            return {
                ...state,
                contacts : state.contacts.map(contact => contact._id === action.payLoad._id ? action.payLoad : contact ) 
            };
        case FILTER_CONTACTS:

            return {
                ...state,
                filtered: state.contacts.filter(contact => {
                    const regex = new RegExp(`${action.payLoad}`, 'gi');
                    return contact.name.match(regex) || contact.email.match(regex);
                })
            };
        case CLEAR_FILTER:

                return {
                    ...state,
                    filtered : null
                };
        case CONTACT_ERROR:

                return {
                    ...state,
                    error : action.payLoad
                };
        case GET_CONTACTS:

                return {
                    ...state,
                    contacts : action.payLoad,
                    loading : false
                };
        case CLEAR_CONTACTS:
            
            return {
                ...state,
                contacts : null,
                filtered : null,
                error : null,
                current : null
            };
        default:
            return state;
    }
}